import React from "react";

import styles from './CreditCard.module.sass';
import Image from "../Image";
import Icon from "../Icon";

const DefaultCard = ({className}) => {
  return (
    <>
      <div className={className}>
        <div className={styles.cardHead}>
        <Image
            className={styles.pic}
            src="/images/logo-light.png"
            srcDark="/images/logo-light.png"
            alt="Core"
          />
        
        <div className={styles.headRight}>
            <Icon name="eye" size="24"></Icon>
            <Icon name="dots" size="24"></Icon>
        </div>
        </div>

        <div className={styles.cardNumber}>
          <span>**** **** ****</span>
          <span>3759</span>
        </div>

        <div className={styles.cardName}>Bruce Wayne</div>

        <div className={styles.cardFooter}>
          <div className={styles.startDate}>10/26</div>
          <div className={styles.endDate}>***</div>
          <Image
            className={styles.cardLogo}
            src="/images/content/visa.png"
            srcDark="/images/content/visa.png"
            alt="visa logo"
          />
        </div>
      </div>
    </>
  );
};



export default DefaultCard;
