import React, { useState } from "react";
import cn from "classnames";
import styles from "./Entry.module.sass";
import TextInput from "../../../components/TextInput";
import Image from "../../../components/Image";
import Icon from "../../../components/Icon"

const Entry = ({ onConfirm }) => {
  const [message, setMessage] = useState('');
  const [error, setError] = useState(null);
  const [valid, setValid] = useState(null);

  function isValidEmail(email) {
    return /\S+@\S+\.\S+/.test(email);
  }

  const handleChange = event => {
    if (!isValidEmail(event.target.value)) {
      setError('Email is invalid');
    } else if (isValidEmail(event.target.value)) {
      setError(null);
      setValid('Valid')
    } else {
      setError(null);
      setValid(null)
    }

    setMessage(event.target.value);
    console.log(event.target.value)
  };

  return (
    <div className={styles.entry}>
      <div className={styles.head}>
        <div className={styles.info}>Sign up with Open account</div>
        <div className={styles.btns}>
          <button className={cn("button-stroke", styles.button)}>
            <img src="/images/content/google.svg" alt="Google" />
            Google
          </button>
          <button className={cn("button-stroke", styles.button)}>
            <Image
              className={styles.pic}
              src="/images/content/apple-dark.svg"
              srcDark="/images/content/apple-light.svg"
              alt="Apple"
            />
            Apple ID
          </button>
        </div>
      </div>
      <div className={styles.body}>
        <div className={styles.info}>Or continue with email address</div>
        <div className={styles.emailWrapper}>
          <TextInput
            className={error ? styles.hasError : styles.field}
            name="email"
            type="email"
            placeholder="Your email"
            required
            icon={error ? "arrow-left" : "mail"}
            value={message}
            onChange={handleChange}
          />

          {error && <Icon name="warning" className={styles.warningIcon} size="24" />}
          {valid && <Icon name="check" className={styles.validIcon} size="24" />}
        </div>

        <button className={cn("button", styles.button)} onClick={valid && onConfirm}>
          Continue
        </button>
        <div className={styles.note}>
          This site is protected by reCAPTCHA and the Google Privacy Policy.
        </div>
      </div>
    </div>
  );
};

export default Entry;
