import React from 'react';
import DemoCard from './Demo';
import AddNew from './AddNew';
import styles from './OtherCards.module.sass'


const OtherCards = () => {
    return (
        <>
            <div className={styles.row}>
                <div className={styles.col}>
                    <DemoCard/>
                </div>
                <div className={styles.col}>
                    <AddNew/>
                </div>
            </div>
        </>
    );
};

export default OtherCards;