import React from "react";
import styles from "./demoCard.module.sass";
import Image from "../../../../../components/Image";

const DemoCard = () => {
  return (
    <>
      <div className={styles.card}>
        <div className={styles.cardHead}>
        <Image
            className={styles.pic}
            src="/images/logo-light.png"
            srcDark="/images/logo-light.png"
            alt="Core"
          />
        </div>

        <div className={styles.cardNumber}>
          <span>**** **** ****</span>
          <span>3759</span>
        </div>

        <div className={styles.cardName}>Clark kent</div>

        <div className={styles.cardFooter}>
          <div className={styles.startDate}></div>
          <div className={styles.endDate}></div>
          <Image
            className={styles.cardLogo}
            src="/images/content/visa.png"
            srcDark="/images/content/visa.png"
            alt="visa logo"
          />
        </div>
      </div>
    </>
  );
};

export default DemoCard;
